import React, { useState } from 'react';
import './brandDashboard.css';
import BrandSidebar from './BrandSidebar';
import BrandDashboardHeader from './BrandDashboardHeader';
import FilterBar from './FilterBar';
import { FaChevronDown, FaChevronUp, FaCity, FaRegCommentDots } from 'react-icons/fa';

const statusOptions = ['New', 'In Progress', 'Closed'];

const sampleInquiries = [
  {
    id: 1,
    name: 'Amit Shah',
    email: 'amit@example.com',
    phone: '9876543210',
    state: 'Gujarat',
    city: 'Ahmedabad',
    message: 'Interested in franchise opportunities.',
    date: '2025-07-01',
    status: 'New',
    remark: ''
  },
  {
    id: 2,
    name: 'Priya Mehta',
    email: 'priya@example.com',
    phone: '9876123456',
    state: 'Maharashtra',
    city: 'Pune',
    message: 'Wants to invest in food sector.',
    date: '2025-07-04',
    status: 'In Progress',
    remark: ''
  }
];

const BrandInquiries = ({ openLoginModal }) => {
  const [expandedRow, setExpandedRow] = useState(null);
  const [visibleContacts, setVisibleContacts] = useState([]);
  const [editModeId, setEditModeId] = useState(null);
  const [editedStatus, setEditedStatus] = useState('');
  const [remark, setRemark] = useState('');
  const [errors, setErrors] = useState({});
  const [inquiries, setInquiries] = useState(sampleInquiries);
  const [filters, setFilters] = useState({
    name: '',
    status: 'All',
    stateId: '',
    stateName: '',
    city: '',
    startDate: '',
    endDate: ''
  });

  const toggleRow = (id) => {
    setExpandedRow(expandedRow === id ? null : id);
  };

  const handleViewContact = (inquiryId) => {
    if (!visibleContacts.includes(inquiryId)) {
      setVisibleContacts(prev => [...prev, inquiryId]);

      fetch('http://localhost/react-api/log-view-contact.php', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          inquiry_id: inquiryId,
          brand_id: 101
        })
      })
        .then(res => res.json())
        .then(data => {
          console.log('Logged contact view:', data);
        })
        .catch(err => console.error('Log error:', err));
    }
  };

  const handleEditClick = (inq) => {
    setEditModeId(inq.id);
    setEditedStatus(inq.status);
    setRemark(inq.remark || '');
    setErrors({});
  };

  const handleSave = (id) => {
    if (!remark.trim()) {
      setErrors({ remark: 'Remark is required' });
      return;
    }

    const updated = inquiries.map(inq =>
      inq.id === id ? { ...inq, status: editedStatus, remark } : inq
    );

    setInquiries(updated);
    setEditModeId(null);
    setRemark('');
    setErrors({});
    console.log('Saved:', { id, status: editedStatus, remark });
  };

  const filteredInquiries = inquiries.filter((inq) => {
    const matchesName = inq.name.toLowerCase().includes(filters.name.toLowerCase());
    const matchesStatus = filters.status === 'All' || inq.status === filters.status;
    const matchesState = !filters.stateName || inq.state === filters.stateName;
    const matchesCity = !filters.city || inq.city === filters.city;

    const inquiryDate = new Date(inq.date);
    const startDate = filters.startDate ? new Date(filters.startDate) : null;
    const endDate = filters.endDate ? new Date(filters.endDate) : null;
    const matchesDate =
      (!startDate || inquiryDate >= startDate) &&
      (!endDate || inquiryDate <= endDate);

    return matchesName && matchesStatus && matchesState && matchesCity && matchesDate;
  });

  return (
    <div className="brand-crm-container">
      <BrandSidebar openLoginModal={openLoginModal} />
      <div className="brand-crm-main">
        <BrandDashboardHeader />

        <div className="inquiries-section">
          <h2>Franchise Inquiries</h2>
          <FilterBar filters={filters} onChange={setFilters} />
          <table className="inquiries-table">
            <thead>
              <tr>
                <th>#</th>
                <th>Name</th>
                <th>Contact</th>
                <th>Status</th>
                <th>State</th>
                <th>Date</th>
                <th>Actions</th>
              </tr>
            </thead>
            <tbody>
              {filteredInquiries.map((inq, index) => (
                <React.Fragment key={inq.id}>
                  <tr className="inquiry-row">
                    <td>{index + 1}</td>
                    <td>{inq.name}</td>
                    <td>
                      {visibleContacts.includes(inq.id) ? (
                        <>
                          📞 {inq.phone} <br /> ✉️ {inq.email}
                        </>
                      ) : (
                        <button
                          className="view-contact-btn"
                          onClick={() => handleViewContact(inq.id)}
                        >
                          View Contact
                        </button>
                      )}
                    </td>
                    <td>
                      {editModeId === inq.id ? (
                        <select
                          value={editedStatus}
                          onChange={(e) => setEditedStatus(e.target.value)}
                        >
                          {statusOptions.map(opt => (
                            <option key={opt} value={opt}>{opt}</option>
                          ))}
                        </select>
                      ) : (
                        <span className={`status-badge ${inq.status.replace(/\s+/g, '').toLowerCase()}`}>
                          {inq.status}
                        </span>
                      )}
                    </td>
                    <td>{inq.state}</td>
                    <td>{inq.date}</td>
                    <td>
                      {editModeId === inq.id ? (
                        <>
                          <input
                            type="text"
                            placeholder="Enter remark"
                            value={remark}
                            onChange={(e) => setRemark(e.target.value)}
                            style={{ width: '120px', marginRight: '5px' }}
                          />
                          <button className="edit-btn" onClick={() => handleSave(inq.id)}>Save</button>
                          {errors.remark && <div className="error-text">{errors.remark}</div>}
                        </>
                      ) : (
                        <>
                          <button className="view-contact-btn" onClick={() => toggleRow(inq.id)}>
                            {expandedRow === inq.id ? 'Hide' : 'View'}
                          </button>{' '}
                          <button className="edit-btn" onClick={() => handleEditClick(inq)}>Edit</button>
                        </>
                      )}
                    </td>
                  </tr>
                  {expandedRow === inq.id && (
                    <tr className="details-row">
                      <td colSpan="7">
                        <div className="inquiry-details">
                          <p><FaCity className="detail-icon" /> <strong>City:</strong> {inq.city}</p>
                          <p><FaRegCommentDots className="detail-icon" /> <strong>Message:</strong> {inq.message}</p>
                          {inq.remark && (
                            <p><strong>Remark:</strong> {inq.remark}</p>
                          )}
                        </div>
                      </td>
                    </tr>
                  )}
                </React.Fragment>
              ))}
            </tbody>
          </table>
        </div>
      </div>
    </div>
  );
};

export default BrandInquiries;