// src/components/BannerCarousel.jsx
import React, { useEffect, useState } from 'react';
import '../components/design/BannerCarousel.css';
import axios from 'axios';

const BannerCarousel = () => {
  const [banners, setBanners] = useState([]);
  const [currentIndex, setCurrentIndex] = useState(0);

  // Fetch banner data from PHP API
  useEffect(() => {
    axios.get('http://localhost/react-api/get-banner.php')
      .then(response => {
        console.log(response);
        setBanners(response.data);
      })
      .catch(error => {
        console.error('Failed to load banners:', error);
      });
  }, []);

  // Auto-slide every 4 seconds
  useEffect(() => {
    const interval = setInterval(() => {
      setCurrentIndex((prevIndex) =>
        prevIndex === banners.length - 1 ? 0 : prevIndex + 1
      );
    }, 4000);
    return () => clearInterval(interval);
  }, [banners]);

  if (banners.length === 0) {
    return <div className="carousel">Loading banners...</div>;
  }

  return (
    <div className="carousel">
      {banners.map((banner, index) => (
        <div
          key={banner.id}
          className={`carousel-slide ${index === currentIndex ? 'active' : ''}`}
        >
         <img src={banner.image_url} alt={banner.title} />


          <div className="carousel-caption">
            <h3>{banner.title}</h3>
          </div>
        </div>
      ))}
    </div>
  );
};

export default BannerCarousel;
