import React, { useState, useEffect } from 'react';
import Select from 'react-select';
import '../design/ConnectNowModal.css';
import {
  FaUser, FaPhone, FaEnvelope, FaMapMarkedAlt,
  FaCity, FaCommentDots, FaTimes
} from 'react-icons/fa';
import brandData from '../../data/brands';
import { validateConnectForm } from '../../utils/validateConnectNowForm';

const ConnectNowModal = ({ onClose }) => {
  const [formData, setFormData] = useState({
    brand_connect_name: '',
    brand_connect_mobile: '',
    brand_connect_email: '',
    brand_connect_state: null,
    brand_connect_city: null,
    brand_connect_message: ''
  });
  const [errors, setErrors] = useState({});
  const [states, setStates] = useState([]);
  const [cities, setCities] = useState([]);
  const [brandName, setBrandName] = useState('...');

  // Get brand_id from URL and load brand name
  useEffect(() => {
    const params = new URLSearchParams(window.location.search);
    const brandId = parseInt(params.get('brand_id'));
    const match = brandData.find((b) => b.id === brandId);
    setBrandName(match ? match.name : 'Unknown Brand');
  }, []);

  useEffect(() => {
    fetch('http://localhost/react-api/get-indian-states.php')
      .then(res => res.json())
      .then(data => {
        const options = data.map(s => ({ label: s.name, value: s.id }));
        setStates(options);
      });
  }, []);

  useEffect(() => {
    if (formData.brand_connect_state) {
      fetch(`http://localhost/react-api/get-cities.php?state_id=${formData.brand_connect_state.value}`)
        .then(res => res.json())
        .then(data => {
          const options = data.map(c => ({ label: c.name, value: c.id }));
          setCities(options);
        });
    }
  }, [formData.brand_connect_state]);

  const handleChange = (e) => {
    setFormData({ ...formData, [e.target.name]: e.target.value });
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    const validationErrors = validateConnectForm(formData);
    if (Object.keys(validationErrors).length > 0) {
      setErrors(validationErrors);
    } else {
      alert("Form submitted successfully!");
      onClose();
    }
  };

  return (
    <div className="modal-backdrop">
      <div className="modal-content">
        <button className="close-btn" onClick={onClose}><FaTimes /></button>
        <h2>Connect with {brandName}</h2>
        <form onSubmit={handleSubmit}>
          <div className="row">
            <div className="form-group">
              <FaUser />
              <input
                type="text"
                name="brand_connect_name"
                placeholder="Full Name"
                value={formData.brand_connect_name}
                onChange={handleChange}
              />
              {errors.brand_connect_name && <p className="error">{errors.brand_connect_name}</p>}
            </div>
            <div className="form-group">
              <FaPhone />
              <input
                type="text"
                name="brand_connect_mobile"
                placeholder="Phone Number"
                value={formData.brand_connect_mobile}
                onChange={handleChange}
              />
              {errors.brand_connect_mobile && <p className="error">{errors.brand_connect_mobile}</p>}
            </div>
          </div>

          <div className="row">
            <div className="form-group">
              <FaEnvelope />
              <input
                type="email"
                name="brand_connect_email"
                placeholder="Email Address"
                value={formData.brand_connect_email}
                onChange={handleChange}
              />
              {errors.brand_connect_email && <p className="error">{errors.brand_connect_email}</p>}
            </div>
            <div className="form-group">
             
              <Select
                placeholder="Select State"
                options={states}
                value={formData.brand_connect_state}
                onChange={(selected) =>
                  setFormData({ ...formData, brand_connect_state: selected, brand_connect_city: null })
                }
              />
              {errors.brand_connect_state && <p className="error">{errors.brand_connect_state}</p>}
            </div>
          </div>

          <div className="row">
            <div className="form-group">
              
              
              <Select
                placeholder="Select City"
                options={cities}
                value={formData.brand_connect_city}
                onChange={(selected) => setFormData({ ...formData, brand_connect_city: selected })}
                isDisabled={!formData.brand_connect_state}
              />
              {errors.brand_connect_city && <p className="error">{errors.brand_connect_city}</p>}
            </div>
            <div className="form-group" style={{ flex: 1 }}>
              <FaCommentDots />
              <textarea
                name="brand_connect_message"
                placeholder="Message"
                value={formData.brand_connect_message}
                onChange={handleChange}
              ></textarea>
              {errors.brand_connect_message && <p className="error">{errors.brand_connect_message}</p>}
            </div>
          </div>

          <button type="submit" className="submit-btn">Submit</button>
        </form>
      </div>
    </div>
  );
};

export default ConnectNowModal;
