export default function validateInvestorForm(form, state, city, sector, agreed) {
  const errors = {};

  if (!form.investor_name.trim()) {
    errors.investor_name = 'Name is required';
  }

  if (!form.investor_mobile) {
    errors.investor_mobile = 'Mobile number is required';
  } else if (!/^[6-9][0-9]{9}$/.test(form.investor_mobile)) {
    errors.investor_mobile = 'Mobile must start with 6/7/8/9 and be 10 digits';
  }

//   if (!form.investor_email) {
//     errors.investor_email = 'Email is required';
//   } else if (!/^[\\w.-]+@[\\w.-]+\\.\\w{2,}$/.test(form.investor_email)) {
//     errors.investor_email = 'Invalid email format';
//   }

  if (!form.investor_password || form.investor_password.length < 6) {
    errors.investor_password = 'Password must be at least 6 characters';
  }

  if (!state) errors.state = 'Please select a state';
  if (!city) errors.city = 'Please select a city';
  if (!sector) errors.sector = 'Please select a sector';

  if (!agreed) errors.terms = 'You must agree to the Terms & Conditions';

  return errors;
}
