// src/components/TeamPage.jsx
import React from 'react';
import TeamMemberCard from './TeamMemberCard';
import '../design/TeamPage.css';

const teamMembers = [
  {
    name: 'Vipul Panchal',
    role: 'Founder & CEO',
    image: 'https://randomuser.me/api/portraits/men/32.jpg',
    bio: 'Leading the vision and innovation with over 10 years of startup experience.',
    linkedin: 'https://linkedin.com/in/nisargtrivedi',
    twitter: 'https://twitter.com/nisargtrivedi',
    instagram: 'https://instagram.com/nisargtrivedi'
  },
  {
    name: 'Priya Panchal',
    role: 'Co Founder',
    image: 'https://randomuser.me/api/portraits/women/68.jpg',
    bio: 'Shaping brand identity through creative design and storytelling.',
    linkedin: 'https://linkedin.com/in/ayeshapatel',
    twitter: '',
    instagram: 'https://instagram.com/ayeshapatel'
  }
];


const TeamPage = () => {
  return (
    <div className="team-page">
      <h1 className="team-title">Meet Our Team</h1>
      <p className="team-subtitle">Passionate minds behind our success</p>
      <div className="team-grid">
        {teamMembers.map((member, index) => (
          <TeamMemberCard key={index} member={member} />
        ))}
      </div>
    </div>
  );
};

export default TeamPage;
