export default function validatePartnerForm(form, state, city, agreed) {
  const errors = {};

  const name = form?.partner_name?.trim?.() || "";
  const mobile = form?.partner_mobile?.trim?.() || "";
  const email = form?.partner_email?.trim?.() || "";
  const password = form?.partner_password?.trim?.() || "";

  if (!name) errors.partner_name = "Full Name is required";
  if (!mobile) errors.partner_mobile = "Mobile number is required";
  if (!email) errors.partner_email = "Email is required";
  if (!password) errors.partner_password = "Password is required";

  if (!state) errors.state = "Please select a state";
  if (!city) errors.city = "Please select a city";
  if (!agreed) errors.terms = "You must agree to the Terms & Conditions";

  return errors;
}
