// src/pages/BrandDetailPage.jsx
import React from 'react';
import Container from '../components/layout/Container';
import BrandImageGallery from '../components/BrandDetail/BrandImageGallery';
import BrandInfo from '../components/BrandDetail/BrandInfo';
import InquiryForm from '../components/BrandDetail/InquiryForm';
import ShareOptions from '../components/BrandDetail/ShareOptions';
import ApplyNowButton from '../components/BrandDetail/ApplyNowButton';
import Hydration from '../components/BrandDetail/HydrationInfo';
import BrandSections from '../components/BrandDetail/BrandSections';
// import IndiaMap from '../components/BrandDetail/IndiaMap';

const dummyBrand = {
  name: 'SnapNBuy Franchise',
  investment: 500000,
  description: 'Become a part of India s fastest-growing furniture eCommerce brand.',
  founded: '2022',
  category: 'Furniture',
  location: 'Ahmedabad, Gujarat',
  images: [
    'https://via.placeholder.com/400x300',
    'https://via.placeholder.com/400x300/aaaaaa',
    'https://via.placeholder.com/400x300/cccccc'
  ],
  url: 'https://snapnbuy.in/brand/snapnbuy'
};

export default function BrandDetailPage() {
  return (
    <Container>
      <div className="brand-detail-page">
        <div className="left-section">
          <BrandImageGallery images={dummyBrand.images} />
          <BrandSections/>
        </div>
        <div className="right-section">
         <ApplyNowButton />
            <BrandInfo brand={dummyBrand} />
            <ShareOptions url={dummyBrand.url} />
            <br/>
            <br/><br/>
            <InquiryForm/>
        </div>
        <Hydration/>
       
      </div>
    </Container>
  );
}