// src/brand/FilterBar.jsx
import React, { useEffect, useState } from 'react';
import './FilterBar.css';

const FilterBar = ({ filters, onChange }) => {
  const [states, setStates] = useState([]);
  const [cities, setCities] = useState([]);

  useEffect(() => {
    fetch('http://localhost/react-api/get-indian-states.php')
      .then(res => res.json())
      .then(data => setStates(data))
      .catch(err => console.error('State load error:', err));
  }, []);

  useEffect(() => {
    if (filters.stateId) {
      fetch(`http://localhost/react-api/get-cities.php?state_id=${filters.stateId}`)
        .then(res => res.json())
        .then(data => setCities(data))
        .catch(err => console.error('City load error:', err));
    } else {
      setCities([]);
    }
  }, [filters.stateId]);

  const handleChange = (e) => {
    const { name, value } = e.target;

    if (name === 'stateId') {
      const selectedState = states.find((s) => String(s.id) === value);
      onChange({
        ...filters,
        stateId: value,
        stateName: selectedState?.name || '',
        city: '',
      });
    } else {
      onChange({ ...filters, [name]: value });
    }
  };

  return (
    <div className="filter-bar">
      <input
        type="text"
        name="name"
        value={filters.name}
        placeholder="Search by name"
        onChange={handleChange}
      />

      <select name="status" value={filters.status} onChange={handleChange}>
        <option value="All">All Status</option>
        <option value="New">New</option>
        <option value="In Progress">In Progress</option>
        <option value="Closed">Closed</option>
      </select>

      <select name="stateId" value={filters.stateId} onChange={handleChange}>
        <option value="">All States</option>
        {states.map((s) => (
          <option key={s.id} value={s.id}>{s.name}</option>
        ))}
      </select>

      <select name="city" value={filters.city} onChange={handleChange}>
        <option value="">All Cities</option>
        {cities.map((c) => (
          <option key={c.id} value={c.name}>{c.name}</option>
        ))}
      </select>

      <input
        type={filters.startDate ? 'date' : 'text'}
        name="startDate"
        value={filters.startDate}
        placeholder="Select Start Date"
        onFocus={(e) => (e.target.type = 'date')}
        onBlur={(e) => {
          if (!filters.startDate) e.target.type = 'text';
        }}
        onChange={handleChange}
      />

      <input
        type={filters.endDate ? 'date' : 'text'}
        name="endDate"
        value={filters.endDate}
        placeholder="Select End Date"
        onFocus={(e) => (e.target.type = 'date')}
        onBlur={(e) => {
          if (!filters.endDate) e.target.type = 'text';
        }}
        onChange={handleChange}
      />
    </div>
  );
};

export default FilterBar;
