// src/brand/BrandDashboard.jsx
import React from 'react';
import './brandDashboard.css';
import BrandSidebar from './BrandSidebar';
import BrandDashboardHeader from './BrandDashboardHeader';
import {
  FaUsers, FaChartLine, FaClipboardList, FaStore
} from 'react-icons/fa';

const BrandDashboard = () => {
  return (
    <div className="brand-crm-container">
      <BrandSidebar />
      <div className="brand-crm-main">
        <BrandDashboardHeader />

        <section className="brand-overview">
          <h2>Brand Performance Overview</h2>
          <div className="stats-grid">
            <div className="stat-card">
              <div className="icon-box purple"><FaStore /></div>
              <div className="text-box">
                <h4>Total Brands</h4>
                <p>58</p>
              </div>
            </div>
            <div className="stat-card">
              <div className="icon-box green"><FaUsers /></div>
              <div className="text-box">
                <h4>Active Enquiries</h4>
                <p>112</p>
              </div>
            </div>
            <div className="stat-card">
              <div className="icon-box blue"><FaChartLine /></div>
              <div className="text-box">
                <h4>Monthly Views</h4>
                <p>3,245</p>
              </div>
            </div>
            <div className="stat-card">
              <div className="icon-box orange"><FaClipboardList /></div>
              <div className="text-box">
                <h4>Pending Approvals</h4>
                <p>9</p>
              </div>
            </div>
          </div>
        </section>
      </div>
    </div>
  );
};

export default BrandDashboard;
