import React, { useEffect, useState } from 'react';
import Select from 'react-select';
import {
  FaUser, FaEnvelope, FaPhone, FaMapMarkedAlt,
  FaCity, FaRegCommentDots
} from 'react-icons/fa';

function InquiryForm() {
  const [formData, setFormData] = useState({
    inquiry_type: 'franchise',
    footer_name: '',
    footer_email: '',
    footer_contact: '',
    footer_state: '',
    footer_city: '',
    footer_message: ''
  });

  const [errors, setErrors] = useState({});
  const [states, setStates] = useState([]);
  const [cities, setCities] = useState([]);
  const [loadingStates, setLoadingStates] = useState(false);
  const [loadingCities, setLoadingCities] = useState(false);

  useEffect(() => {
    setLoadingStates(true);
    fetch('http://localhost/react-api/get-indian-states.php')
      .then(res => res.json())
      .then(data => {
        if (Array.isArray(data)) {
          const formatted = data.map(s => ({ label: s.name, value: s.id }));
          setStates(formatted);
        }
      })
      .catch(err => console.error('State fetch error:', err))
      .finally(() => setLoadingStates(false));
  }, []);

  useEffect(() => {
    if (formData.footer_state) {
      setLoadingCities(true);
      fetch(`http://localhost/react-api/get-cities.php?state_id=${formData.footer_state}`)
        .then(res => res.json())
        .then(data => {
          if (Array.isArray(data)) {
            const formatted = data.map(city => ({ label: city.name, value: city.id }));
            setCities(formatted);
          }
        })
        .catch(err => console.error('City fetch error:', err))
        .finally(() => setLoadingCities(false));
    } else {
      setCities([]);
    }
  }, [formData.footer_state]);

  const handleInputChange = e => {
    const { name, value } = e.target;
    setFormData(prev => ({ ...prev, [name]: value }));
  };

  const handleStateChange = selectedOption => {
    setFormData(prev => ({
      ...prev,
      footer_state: selectedOption?.value || '',
      footer_city: ''
    }));
  };

  const handleCityChange = selectedOption => {
    setFormData(prev => ({
      ...prev,
      footer_city: selectedOption?.value || ''
    }));
  };

  const validate = () => {
    const newErrors = {};
    if (!formData.footer_name) newErrors.footer_name = 'Name is required';
    if (!formData.footer_email || !/\S+@\S+\.\S+/.test(formData.footer_email)) newErrors.footer_email = 'Valid email required';
    if (!formData.footer_contact) newErrors.footer_contact = 'Contact number is required';
    if (!formData.footer_state) newErrors.footer_state = 'State is required';
    if (!formData.footer_city) newErrors.footer_city = 'City is required';
    if (!formData.footer_message) newErrors.footer_message = 'Message is required';
    return newErrors;
  };

  const handleSubmit = e => {
    e.preventDefault();
    const validationErrors = validate();
    if (Object.keys(validationErrors).length > 0) {
      setErrors(validationErrors);
    } else {
      alert('Thank you for your inquiry!');
      console.log(formData);
    }
  };

  return (
    <div className="inquiry-form-container">
      <h4>Connect With Us</h4>

      <form className="inquiry-form" onSubmit={handleSubmit}>
        <div className="radio-group">
          <label>
            <input
              type="radio"
              name="inquiry_type"
              value="franchise"
              checked={formData.inquiry_type === 'franchise'}
              onChange={handleInputChange}
            /> Franchise Inquiry
          </label>
          <label>
            <input
              type="radio"
              name="inquiry_type"
              value="expand"
              checked={formData.inquiry_type === 'expand'}
              onChange={handleInputChange}
            /> Expand Your Brand
          </label>
        </div>

        <div className="row">
          <div className="form-field">
            <FaUser className="form-icon" />
            <input
              type="text"
              name="footer_name"
              placeholder="Full Name *"
              value={formData.footer_name}
              onChange={handleInputChange}
            />
            {errors.footer_name && <span className="error">{errors.footer_name}</span>}
          </div>

          <div className="form-field">
            <FaEnvelope className="form-icon" />
            <input
              type="email"
              name="footer_email"
              placeholder="Email *"
              value={formData.footer_email}
              onChange={handleInputChange}
            />
            {errors.footer_email && <span className="error">{errors.footer_email}</span>}
          </div>
        </div>

        <div className="row">
          <div className="form-field">
            <FaPhone className="form-icon" />
            <input
              type="tel"
              name="footer_contact"
              placeholder="Contact Number *"
              value={formData.footer_contact}
              onChange={handleInputChange}
            />
            {errors.footer_contact && <span className="error">{errors.footer_contact}</span>}
          </div>

          <div className="form-field">
            <div className="custom-select-container">
              <Select
                options={states}
                value={states.find(s => s.value === formData.footer_state) || null}
                onChange={handleStateChange}
                placeholder={loadingStates ? 'Loading states...' : 'Select State *'}
                isSearchable
              />
            </div>
            {errors.footer_state && <span className="error">{errors.footer_state}</span>}
          </div>
        </div>

        <div className="row">
          <div className="form-field">
            <div className="custom-select-container">
              <Select
                options={cities}
                value={cities.find(c => c.value === formData.footer_city) || null}
                onChange={handleCityChange}
                placeholder={loadingCities ? 'Loading cities...' : 'Select City *'}
                isSearchable
                isDisabled={!formData.footer_state}
              />
            </div>
            {errors.footer_city && <span className="error">{errors.footer_city}</span>}
          </div>

          <div className="form-field">
            <FaRegCommentDots className="form-icon" />
            <textarea
              name="footer_message"
              placeholder="Your Message *"
              rows="3"
              value={formData.footer_message}
              onChange={handleInputChange}
            />
            {errors.footer_message && <span className="error">{errors.footer_message}</span>}
          </div>
        </div>

        <button type="submit" className="submit-btn">Submit Request</button>
      </form>

      {/* 🔽 Embedded CSS for quick use */}
      <style>{`
        .inquiry-form-container {
          max-width: 800px;
          margin: auto;
          background: #f9f9f9;
          padding: 30px;
          border-radius: 10px;
          box-shadow: 0 4px 12px #27235a;
          font-family: 'Segoe UI', sans-serif;
        }

        h4 {
          text-align: center;
          margin-bottom: 25px;
          font-size: 24px;
          color: #333;
        }

        .radio-group {
          display: flex;
          justify-content: center;
          gap: 30px;
          margin-bottom: 25px;
        }

        .radio-group label {
          font-size: 15px;
          color: #555;
        }

        .row {
          display: flex;
          flex-wrap: wrap;
          gap: 20px;
          margin-bottom: 20px;
        }

        .form-field {
          flex: 1;
          position: relative;
          display: flex;
          flex-direction: column;
        }

        .form-icon {
          position: absolute;
          top: 12px;
          left: 10px;
          color: #777;
          pointer-events: none;
        }

        .form-field input,
        .form-field textarea {
          padding: 10px 10px 10px 35px;
          border: 1px solid #ccc;
          border-radius: 5px;
          font-size: 15px;
          width: 100%;
        }

        .form-field textarea {
          min-height: 70px;
          resize: vertical;
        }

        .form-field .error {
          color: red;
          font-size: 12px;
          margin-top: 5px;
        }

        .submit-btn {
          width: 100%;
          padding: 12px;
          background: #27235a;
          color: white;
          font-size: 16px;
          font-weight: bold;
          border: none;
          border-radius: 6px;
          cursor: pointer;
        }

        .submit-btn:hover {
          background: #218838;
        }

        @media (max-width: 768px) {
          .row {
            flex-direction: column;
          }
        }
      `}</style>
    </div>
  );
}
export default InquiryForm;
