import React from 'react';
import BrandCard from '../components/BrandCard';
import brands from '../data/brands';
import FreeAdviceButton from '../components/BrandListing/FreeAdviceButton';

import '../components/design/BrandCard.css';

const BrandListing = () => {
  
  const limitedBrands = brands.slice(0); // Show all brands

  return (
    <div style={{ padding: '20px' }}>
      {/* Heading */}
      <div
        style={{
          display: 'flex',
          justifyContent: 'space-between',
          alignItems: 'center',
          marginBottom: '20px'
        }}
      >
       
        <h2 style={{ margin: 0 }}>Leading Franchises Today</h2>
         <FreeAdviceButton/>
        
      </div>
      

      {/* Grid of brand cards */}
      <div
        style={{
          display: 'grid',
          gridTemplateColumns: 'repeat(auto-fit, minmax(260px, 1fr))',
          gap: '20px'
        }}
      >
        {limitedBrands.map((brand, index) => (
          <BrandCard key={index} brand={brand} />
        ))}
      </div>

     

    </div>
  );
};

export default BrandListing;
