// src/App.js
import React from 'react';
import {
  BrowserRouter as Router,
  Routes,
  Route,
  useLocation,
} from 'react-router-dom';

// Page Components
import HomePage from './pages/HomePage';
import BrandDetail from './pages/BrandDetailPage';
import BrandListing from './pages/BrandListing';
import CookiePolicy from './pages/CookiePolicy';
import PrivacyPolicy from './pages/PrivacyPolicy';
import TermsOfUse from './pages/TermsOfUse';
import PaymentTerms from './pages/PaymentTerms';

// Static Sections
import Header from './components/Header';
import Footer from './components/Footer';
import AboutPage from './components/AboutPage';
import TeamPage from './components/Team/TeamPage';

// Brand Dashboard Routes
import BrandDashboard from './brand/BrandDashboard';
import BrandInquiries from './brand/BrandInquiries';
import BrandProfile from './brand/BrandProfile';

// Optional: PWA Install Component
import InstallPWA from './components/InstallPWA'; // If using install prompt

function AppContent() {
  const location = useLocation();

  // Define routes that do not require Header/Footer
  const hideLayoutRoutes = [
    '/brand/dashboard',
    '/brand/inquiries',
    '/brand/profile',
  ];

  const shouldHideLayout = hideLayoutRoutes.includes(location.pathname);

  return (
    <>
      {!shouldHideLayout && <Header />}

      <Routes>
        <Route path="/" element={<HomePage />} />
        <Route path="/brands" element={<BrandListing />} />
        <Route path="/brand/:id" element={<BrandDetail />} />
        <Route path="/about" element={<AboutPage />} />
        <Route path="/our-team" element={<TeamPage />} />
        <Route path="/cookies-policy" element={<CookiePolicy />} />
        <Route path="/privacy-policy" element={<PrivacyPolicy />} />
        <Route path="/terms-of-use" element={<TermsOfUse />} />
        <Route path="/payment-terms" element={<PaymentTerms />} />

        {/* Brand Dashboard Routes */}
        <Route path="/brand/dashboard" element={<BrandDashboard />} />
        <Route path="/brand/inquiries" element={<BrandInquiries />} />
        <Route path="/brand/profile" element={<BrandProfile />} />
      </Routes>

      {!shouldHideLayout && <Footer />}

      {/* Optional: Install PWA Button */}
      <InstallPWA />
    </>
  );
}

function App() {
  return (
    <Router>
      <AppContent />
    </Router>
  );
}

export default App;
